import React, {useState,useEffect, useRef} from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import Menu from './Components/Menu'
import Home from './Components/Pages/Home'
import How from './Components/Pages/How'
import Portfolio from './Components/Pages/Portfolio'
import Contact from './Components/Pages/Contact'
import avatar from './av.jpg'


export const funnyLinks = () => {
  const anchors = document.querySelectorAll('.funny-link');
  /* for ( let i = 0; i < anchors.length; i++ ) {
    anchors[]
  } */
  Array.from(anchors).forEach( (anchor) => {
    const anchorBody = anchor.innerText.replace(/([aeiouAEIOU])/g, '<span style="transition-delay="0.3s">$1</span>');
    anchor.innerHTML = anchorBody;
    for ( let i = 0; i < anchor.getElementsByTagName('span').length; i++) {
      anchor.getElementsByTagName('span')[i].style.animationDelay = "0."+i+"s";
    };

  });
}

const App = props => {

  const isInitialMount = useRef(true);

  function detectCurrentStep() {
    const location = window.location.pathname.split('/')[1];
    switch(location) {
      case '':
        return 0
      case 'how':
        return 1
      case 'portfolio':
        return 2
      case 'contact':
        return 3
      default:
        return
    }
  }

  const initialStep =  detectCurrentStep();
  
  

  const [step, setStep] = useState( initialStep );
  

  function changeStep(step) {
    window.scrollTo(0,0); 
    setStep( step );
  }

  useEffect( () => {

      if (isInitialMount.current) {
        isInitialMount.current = false;
        document.querySelector('.avatar').style.transitionDuration = "0s";
      } else {
         document.querySelector('.avatar').style.transitionDuration = "inherit";
      }

      funnyLinks();
      
      // Define elements
      var root = document.documentElement, 
      frameBg = document.querySelector('.frameBg'),
      body = document.getElementsByTagName('body')[0];

      // Define helper functions
      function map_range(value, low1, high1, low2, high2) {
          return low2 + (high2 - low2) * (value - low1) / (high1 - low1);
      }
      function getRandomInt(min, max) {
          return Math.floor(Math.random() * (max - min + 1)) + min;
      }
      root.addEventListener('mousemove', (e) => {
        var vw = window.innerWidth;
        var vh = window.innerHeight;
        var hue;
        //let hue = Math.floor( map_range(e.pageY, 0, vw, 32, 60 ) );
        if ( vh > vw ) {
          hue = Math.floor( map_range(e.pageX, 0, vh, 140, 260 ) );
        } else {
          hue = Math.floor( map_range(e.pageY, 0, vh, 140, 260 ) );
        }
        
        root.style.setProperty('--secondary-color', 'hsl( '+hue+', 48%, 53%)');
      });

      function setSecondaryHue() {
        let hue = getRandomInt(140, 260);
        root.style.setProperty('--secondary-color', 'hsl( '+hue+', 48%, 53%)');
      }
      setSecondaryHue();

      function scaleFrameToFullSize() {
        var frameBgW = frameBg.offsetWidth;
        var frameBgH = frameBg.offsetHeight;
        var scaleX = window.innerWidth / frameBgW;
        var scaleY = window.innerHeight / frameBgH; 

        frameBg.style.setProperty('--scale-x', scaleX.toFixed(2));
        frameBg.style.setProperty('--scale-y', scaleY.toFixed(2));   
      }

      body.style.setProperty('height', window.innerHeight+'px');

      setTimeout(function(){
        scaleFrameToFullSize();
      }, 400)
      

      window.addEventListener('resize', function(){
        scaleFrameToFullSize();
        body.style.setProperty('height', window.innerHeight+'px');
      });

  }, [step]); 

  

  return (
    <div className="siteBody" data-step={ step }>
      
      <div className="bodyBg"></div>
      <div className="frameBg" >
          <div className="frameBg__border">
            <svg>
              <rect width="100%" height="100%" />
            </svg>
          </div>
          <div className="avatar">
            <img src={avatar} alt="this is me" />
          </div>
      </div>

      <Router>
          <Menu action={changeStep} />
          <Switch> 
            <Route path="/" component={Home} exact/>
            <Route path="/how" component={How} />
            <Route path="/portfolio" component={Portfolio} />
            <Route path="/contact" component={Contact} />
          </Switch>          
      </Router>
       
    </div>
  );
}

export default App;
